/* Slider */
.slick-slider
{
    position: relative;

    display: block;

    -moz-box-sizing: border-box;
         box-sizing: border-box;

    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
        touch-action: pan-y;
    -webkit-tap-highlight-color: transparent;
}

.slick-list
{
    position: relative;

    display: block;
    overflow: hidden;

    margin: 0;
    padding: 0;
}
.slick-list:focus
{
    outline: none;
}
.slick-list.dragging
{
    cursor: pointer;
    cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list
{
    .transform(translate3d(0, 0, 0));
}

.slick-track
{
    position: relative;
    top: 0;
    left: 0;

    display: block;
}
.slick-track:before,
.slick-track:after
{
    display: table;

    content: '';
}
.slick-track:after
{
    clear: both;
}
.slick-loading .slick-track
{
    visibility: hidden;
}

.slick-slide
{
    display: none;
    float: left;

    height: 100%;
    min-height: 1px;
}
[dir='rtl'] .slick-slide
{
    float: right;
}
.slick-slide img
{
    display: block;
}
.slick-slide.slick-loading img
{
    display: none;
}
.slick-slide.dragging img
{
    pointer-events: none;
}
.slick-initialized .slick-slide
{
    display: block;
}
.slick-loading .slick-slide
{
    visibility: hidden;
}
.slick-vertical .slick-slide
{
    display: block;

    height: auto;

    border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
    display: none;
}

.slick-prev,
.slick-next {
    display: block;

    position: absolute;
    bottom: 0;
    height: 34px;
    width: 126px;
    
    opacity: 0.7;
    background-color: transparent;
    
    cursor: pointer;
    z-index: 1;

    @media(max-width: @xs-max){
        top: 50%;
    }

    &:hover, 
    &:focus {
        opacity: 1;
    }

    svg{
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        height: 100%;
        .translate(-50%, -50%);
    }

    &.slick-disabled {
        opacity: 0.5;
    }
}

.slick-prev {
    left: 0;
    @media (max-width: @xs-max){
        .translate(-65%, -50%);
    }
}

.slick-next {
    right: 0;
    @media (max-width: @xs-max){
        .translate(65%, -50%);
    }
}
