&-select{
    position: relative;
    select{
        padding-right: 32px;
        padding-top: 4px;
        padding-bottom: 4px;
        cursor: pointer;
    }
    &-icon,
    &-no-icon:after{
        position: absolute;
        top: 50%;
        .translate(0, -50%);
        pointer-events: none;
    }
    &-icon{
        width: 1.2em;
        height: 90%;
        fill: @input-color;
        right: 7px;
        svg{
            width: 100%;
            height: 100%;
        }
    }
    &-no-icon:after{
        content: '▼';
        color: @input-color;
        right: 12px;
    }
    &__disabled{
        cursor: not-allowed;
    }

    &__disabled &-icon,
    &__disabled &-no-icon:after{
        color: @input-disabled-color;
        fill: @input-disabled-color;
    }
}